@import '../common/styles/sass/mixins';

.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 80px;
  padding: 0;
  z-index: 10;

  .headerContainer {
    @include container();
    justify-content: flex-end;

    @media (max-width: 768px) {
      justify-content: center;
    }
  }
}
